.top-container {
    background-color: #007B83;
    color: white;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-content: center;
    gap: 35px;
    padding: 10px 0px 10px 60px;
}

.top-container a {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
    gap: 10px;
}

nav {
    background-color: #fff;
    display: flex;
    align-items: center;
    padding-left: 60px;
}

.main-container {
    width: 100%;
}

.main-container ul {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    /* align-items: center; */
    list-style-type: none;
    justify-content: space-evenly;
}

.main-container ul li {
    position: relative;
    color: #027B83;
    padding: 10px;
    cursor: pointer;
}

.main-container ul li a {
    position: relative;
    display: flex;
    align-items: center;
}

.main-container ul li a:after {
    content: url('../assests/arrow.png');
    margin-left: 10px;
}

.main-container ul li.active a:after {
    content: url('../assests/arrow-up.png');
}

.dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    background: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border: 1px solid #ddd;
    padding: 10px;
    z-index: 10;
}

.dropdown ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
}

.dropdown ul li {
    padding: 8px 15px;
    color: #007B83;
    cursor: pointer;
}

.dropdown ul li:hover {
    background-color: #f4f4f4;
}

.btn {
    width: 70px;
    height: 28px;
    border-radius: 9px;
    border: 1px solid transparent;
    background-color: #027B83;
    color: white;
}

.btn:hover {
    background-color: #25969e;
    cursor: pointer;
}


/* Responsive for  1024 px*/
@media only screen and (max-width: 1024px) {
    .form-container input {
        width: 41%;
        border: 1px solid grey;
        border-radius: 12px;
        text-align: left;
        padding: 10px 10px;
    }
}

.hamburger {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0 auto;
    /* margin: 0 auto; */
    /* position: absolute; */
    left: 222px;
}

.closeIcon {
    position: absolute;
    /* left: 180px; */
    display: none;
    font-size: 21px;
    right: 16px;
    color: #007B83;
}


/* Responsive for  1024 px*/
@media only screen and (max-width:768px) {

    /* nav {
        background-color: #fff;
        display: none;
        align-items: center;
        padding-left: 60px;
    } */
    nav {
        background-color: #fff;
        display: flex;
        padding-left: 60px;
        justify-content: space-between;
        align-items: center;
    }

    .main-container {
        display: none;
    }

    .hamburger {
        display: block;
    }

    .main-container.menu-active {
        background-color: #fff;
        display: flex;
        height:90vh;
        padding: 36px 0px;
        position: fixed;
        right: 0;
        top: 0;
        width: 60%;
        z-index: 1;
        box-shadow: 2px 2px 13px 2px grey;
        overflow-y: scroll;
    }


.main-container.menu-active ul {
    flex-direction: column;
    gap: 10px;
    text-align: left !important;
    padding-left: 20px;
}

.main-container.menu-active ul li {
    color: #007B83;
    text-align: left;
}

.main-container.menu-active ul li a {
    /* color: #007B83; */
    text-align: left;
}


.dropdown {
    position: relative !important;
    top: 0px;
    left: 0;
    background: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border: 1px solid #ddd;
    padding: 10px;
    z-index: 10;
}

.closeIcon {
    display: block;
}


}

/* Responsive for  375 px*/
@media only screen and (max-width:580px) {
    .closeIcon {
        display: block;
    }

    .top-container {
        background-color: #007B83;
        color: white;
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        align-content: center;
        gap: 8px;
        padding: 7px 0px 10px 38px;
    }
}

@media only screen and (max-width:414px) {
    .closeIcon {
        display: block;
    }

    .top-container {
        background-color: #007B83;
        color: white;
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        align-content: center;
        gap: 8px;
        padding: 10px 0px 10px 38px;
    }
}